const tools = require('../../common/tools.js');
import empty from '../empty/empty.vue';
export default {
	components:{
		empty
	},
	data() {
		return {
			cartList: []
		}
	},
	onLoad() {
		// this.getList();
	},
	methods: {
		getList(){
			let that = this;
			let params = {
				pageSize: 10,
				pageNumber: 1
			}
			tools.request("/api/drug/myCart.json", params,1,true).then(function(data) {
				that.cartList = data.myCartList;
			})
		}
	}
}
